.App {
  margin: 0 auto;
  max-width: 1200px;
  text-align: center;
  display: flex;
  justify-content: center;
}

.section-wrapper {
  margin: 0 auto;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

h2 {
  margin: 0 0 30px 0;
  padding: 0;
  text-align: center;
}

h3  {
  margin: 0 0 20px 0;
  padding: 0;

}

button {
  width: 150px;
  height: 40px;
  border: 2px solid black;
  background-color: transparent;
  text-transform: uppercase;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

