.Work_work_background__xg9ul {
    padding: 15px;
    margin-bottom: 45px;
    width: 330px;

    display: block;

    background-color: #161616;
    position: relative;
}

.Work_work_background__xg9ul:hover {
    transform: translate(0px, -20px);
    box-shadow: -10px -10px 0px 0px rgba(221, 45, 100, 0.4);
    background-image: linear-gradient(45deg, #E0040B 15%, #dd1e4b 65%);
    transition-timing-function: cubic-bezier(.25, .25, .75, .75);
    /*
    transition-duration: 0.4s;
    */
    /*
    transition-property: opacity, transform;
    */
    transition: all 0.5s ease-in-out 0s;

}

.Work_work_background__xg9ul:before {
    content: " ";
    height: 200px;
    width: 200px;
    background-image: linear-gradient(45deg, #E0040B 15%, #dd1e4b 65%);


    z-index:-2;
    position: absolute;
    top: -10px;
    left: -10px;
}

.Work_work_background__xg9ul:hover:before {
    /*transform: translate(20px, 20px);*/
    height: 100%;
    width: 100%;

    opacity: 0;
    transition-timing-function: cubic-bezier(.25, .25, .75, .75);
    transition-duration: 0.4s;
    transition-property: opacity, transform;
    transition: all 0.5s ease-in-out 0s;
}


